                this.deleteSuccess(response);
              })
              .catch((error) => {
                this.$Message.error('删除失败');
                this.modal6loading = false
              });
            break;
        }

      }
      ,
      remove(index) {
        switch (this.witch) {
          case "0":
            this.morning_data.splice(index, 1);
            break;
          case "1":
            this.night_data.splice(index, 1);
            break;
          case "2":
            this.question_data.splice(index, 1);
            break;
          case "3":
            this.udid_data.splice(index, 1);
            break;
          case "4":
            this.ask_data.splice(index, 1);
            break;
          case "5":
            this.app_data.splice(index, 1);
            break;
        }


      }
      ,
      init() {
        this.getData()
      }
      ,

      addMorning() {
        this.$Modal.confirm({
          render: (h) => {
            return h('Input', {
              props: {
                value: this.value,
                autofocus: true,
                placeholder: '输入早安内容',
                type: 'textarea',
                rows: 6
              },
              on: {
                input: (val) => {
                  this.value = val;
                }
              }
            })
          },
          onOk: () => {
            console.log(this.value);
            let postData = this.$qs.stringify({
              text: this.value,
              type: 0
            });

            this.$http.post('/duyao', postData)
              .then((response) => {
                if (response.data.code == 200) {
                  this.$Message.success(response.data.msg);
                  this.getData()
                } else {
                  this.$Message.error(response.data.msg);
                }


              }).catch((error) => {
              this.$Message.error('添加失败');
            });
            this.value = ""
          }
        })
      }
      ,
      addNight() {
        this.$Modal.confirm({
          render: (h) => {
            return h('Input', {
              props: {
                value: this.value,
                autofocus: true,
                placeholder: '输入晚安内容',
                type: 'textarea',
                rows: 6
              },

              on: {
                input: (val) => {
                  this.value = val;
                }
              }
            })
          },
          onOk: () => {
            console.log(this.value);
            let postData = this.$qs.stringify({
              text: this.value,
              type: 1
            });

            this.$http.post('/duyao', postData)
              .then((response) => {
                this.$Message.success('添加成功');
                this.getData();

              }).catch(function (error) {
              this.$Message.success('添加失败');
            });
            this.value = ""
          }
        })
      }
      ,
      addQuestion() {
        this.$Modal.confirm({
          render: (h) => {
            return h('div', [
              h('Input', {
                props: {
                  value: this.title,
                  autofocus: true,
                  placeholder: '输入问题内容',
                  type: 'textarea',
                  rows: 2
                },

                on: {
                  input: (val) => {
                    this.title = val;
                  }
                }
              }), h('br', {}), h('br', {}), h('Input', {
                props: {
                  value: this.value,
                  autofocus: true,
                  placeholder: '输入答案内容',
                  type: 'textarea',
                  rows: 6
                },

                on: {
                  input: (val) => {
                    this.value = val;
                  }
                }
              }), h('br', {}), h('br', {}), h('Input', {
                props: {
                  value: this.image,
                  autofocus: true,
                  placeholder: '输入图片名称'
                },

                on: {
                  input: (val) => {
                    this.image = val;
                  }
                }
              })
            ]);
          },
          onOk: () => {

            let postData = this.$qs.stringify({
              question: this.title,
              result: this.value,
              image: this.image
            });

            this.$http.post('/question', postData)
              .then((response) => {
                if (response.data.code === 200) {
                  this.$Message.success('添加成功');
                  this.getData();
                  this.value = "";
                  this.title = "";
                  this.image = '';
                } else {
                  this.$Message.error(response.data.msg);
                }


              }).catch((error) => {
              console.log(error);
              this.$Message.success('添加失败');
            });

          }
        })
      }
      ,
      showLogin() {
        this.$Modal.confirm({
          title: '登录',
          okText: '登录',
          render: (h) => {

            return h('div', {}, [h('br'), h('Input', {
              props: {
                value: this.value,
                autofocus: true,
                placeholder: '请输入手机号码'
              },
              on: {
                input: (val) => {
                  this.value = val;
                }
              }
            }), h('br'), h('br'), h('Input', {
              props: {
                value: this.password,
                autofocus: true,
                placeholder: '请输入密码'
              },
              on: {
                input: (val) => {
                  this.password = val;
                }
              }
            })]);

          },
          onOk: () => {

            this.$http.get('/user/login?phone=' + this.value)
              .then((response) => {
                this.value = '';
                if (response.data.code == 200) {

                  this.token = response.data.data.token;
                  localStorage.setItem('youheng.leefeng.top', this.token);
                  this.getData()
                } else {
                  console.log(response.data);
                  this.$Message.error(response.data.msg);
                }


              }).catch((error) => {
              this.$Message.success(error.message);
            });
            this.value = "";
            this.password = '';
          }

        })
      }
      ,
      addApp() {
        this.modal_title = "添加APP信息";
        this.appEdit = true;
      },
      confirmApp() {

        let postData = {
          name: this.title,
          version: this.value,
          size: this.image,
          url: this.appurl,
          uuid: this.uuid,
          build:this.app_build,
          describe:this.app_describe
        };

        if (this.modal_title === "修改APP信息") {
          this.$http.put('/app', postData, {headers: {"Content-Type": "application/json"}})
            .then((response) => {
              if (response.data.code === 200) {
                this.$Message.success('修改成功');
                this.getData();
                this.title = "";
                this.value = "";
                this.image = "";
                this.appurl = "";
              } else {
                this.$Message.error(response.data.msg);
              }


            }).catch((error) => {
            console.log(error);
            this.$Message.success('修改失败');
          });

        } else {


          this.$http.post('/app', postData)
            .then((response) => {
              if (response.data.code === 200) {
                this.$Message.success('添加成功');
                this.getData();
                this.title = "";
                this.value = "";
                this.image = "";
                this.appurl = "";
              } else {
                this.$Message.error(response.data.msg);
              }


            }).catch((error) => {
            console.log(error);
            this.$Message.success('添加失败');
          });

        }
        this.modal_title = "";
      },
      cancleApp() {
        this.title = "";
        this.value = "";
        this.image = "";
        this.appurl = "";
      },
      showAppEditAlert(i) {
        let app = this.getValue(i);
        this.uuid = app['uuid'];
        this.title = app['name'];
        this.value = app['version'];
        this.image = app['size'];
        this.appurl = app['url'];
        this.app_build = app['build'];
        this.app_describe = app['describe'];
        this.appEdit = true;
        this.modal_title = "修改APP信息";


      },
      confirmImage() {
        this.image = "";
        this.modal_title = "";
      },
      getData() {
        this.loading = true;
        switch (this.witch) {
          case "0":
            this.$http.get('/duyao?isM=0')
              .then((response) => {
                this.morning_data = response.data.data;
                this.loading = false
                // if (m.clientMessage) {
                //   this.$Message.success('获取数据成功');
                // }
              })
              .catch((error) => {
                console.log(error);
                this.loading = false;
                this.$Message.error('获取数据失败');
                this.showLogin()
              });
            break;
          case "1":
            this.$http.get('/duyao?isM=1')
              .then((response) => {
                this.night_data = response.data.data;
                this.loading = false;
                // if (m.clientMessage) {
                this.$Message.success('获取数据成功');
                // }
              })
              .catch((error) => {
                console.log(error);
                this.loading = false;
                this.$Message.error(error.message);
                this.showLogin();
              });
            break;
          case "2":
            this.$http.get('/question')
              .then((response) => {
                this.question_data = response.data.data;
                this.loading = false;
                // if (m.clientMessage) {
                this.$Message.success('获取数据成功');
                // }
              })
              .catch((error) => {
                console.log(error);
                this.loading = false;
                this.$Message.error(error.message);
                this.showLogin();
              });
            break;
          case "3":
            this.$http.get('/udidm')
              .then((response) => {
                this.loading = false;
                this.udid_data = response.data.data;
                this.$Message.success('获取数据成功');
              }).catch((error) => {
              this.loading = false;
              this.$Message.error(error.message);
            });
            break;
          case "4":
            this.$http.get('/ask')
              .then((response) => {
                this.loading = false;
                this.ask_data = response.data.data;
                this.$Message.success('获取数据成功');
              }).catch((error) => {
              this.loading = false;
              this.$Message.error(error.message);
            });
            break;
          case "5":
            this.$http.get('/app')
              .then((response) => {
                this.loading = false;
                this.app_data = response.data.data;
                this.$Message.success('获取数据成功');
              }).catch((error) => {
              this.loading = false;
              this.$Message.error(error.message);
            });
            break;
        }


      }
      ,

      tabClick(name) {
        switch (this.witch) {
          case "0":
            if (this.morning_data.length === 0) {
              this.getData();
            }
            break;
          case "1":
            if (this.night_data.length === 0) {
              this.getData()
            }
            break;
          case "2":
            if (this.question_data.length === 0) {
              this.getData();
            }
            break;
          case "3":
            if (this.udid_data.length === 0) {
              this.getData();
            }
            break;
          case "4":
            if (this.ask_data.length === 0) {
              this.getData();
            }
            break;
          case "5":
            if (this.app_data.length === 0) {
              this.getData();
            }
            break;
        }
      }
      ,
      getAllPositionDetails(showMessage) {
        this.$socket.emit('getAllPositionDetails', showMessage)
      }
      ,
      exportData(type) {
        if (type === 3) {
          this.$refs.table_positions.exportCsv({
            filename: 'Custom data',
            columns: this.position_col.filter((col, index) => index < 4),
            data: this.position_data.filter((data, index) => index < 4)
          });
        }
      }
      ,

      goodTime(str) {
        if (typeof str === "undefined" || str === null || str === "") {
          return '';
        }

        let oldTime = new Date(str);
        if (oldTime.getFullYear() < 2018) {
          return '';
        }

        return oldTime.getFullYear() + '-' + (oldTime.getMonth() + 1) + '-' + oldTime.getUTCDate() + ' ' + checkTime(oldTime.getUTCHours()) + ':' + checkTime(oldTime.getMinutes()) + ':' + checkTime(oldTime.getSeconds());
      }
    }
  }

  function checkTime(t) {
    if (t < 10) {
      t = '0' + t;
    }
    return t;
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: left;
    color: #2c3e50;
    margin: 0px;
  }

  #h2_title {
    color: white;
  }

  #divImg {

    width: 100%;
    position: fixed;
    top: 0;

    display: none;
    height: 100%;
  }

  #imgShow {
    max-width: 800px;
    /*max-height: 600px;*/

  }

  #divImgBac {
    background-color: #000000;
    opacity: 0.7;
    width: 100%;
    height: 100%;
  }

  #divImgFather {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    text-align: center;
    top: 0;
    overflow-y: auto;
    overflow-x: auto;
  }
</style>



